}
.searchedUserExpedition::-webkit-scrollbar {
  display: none;
}
.searchedUserExpeditionClose {
  float : right;
  cursor : pointer;
}
.userExpeditionChar {
  width : 50%;
  margin-bottom : .5rem;
  padding : .5rem;
  border-radius : var(--contBox-border-radius);
  cursor : pointer;
}
.userExpeditionChar:hover {
  background : var(--bgLow);
}
.userExpedition {
  margin-bottom : 1rem;
}
.userExpeditionServer {
  font-weight : bold;
  margin-bottom : 1rem;
}
.userExpeditionCharWrap {
  display : flex;
  flex-wrap : wrap;
}
.mainTabWrap, .subTabWrap {
  background : var(--bgDeep);
  padding : .5rem;
  display : flex;
  width : -webkit-fit-content;
  width : -moz-fit-content;
  width : fit-content;
  border-radius : 5px 5px 0 0;
}
.mainTabWrap .tab {
  margin-right : 2rem;
}
.subTabWrap {
  border-radius : 0;
  width : 100%;
  border-radius : 0 5px 0 0;
}
.tab {
  flex-grow : 1;
  color : #666;
  cursor : pointer;
}
.tab * {
  color : #666;
}

.white, .white * {
  color : white;
}
.doubleColumnWrap {
  display : flex;
}
.doubleColumnWrap > div {
  width : calc((100% - .5rem -0.1px) / 2);
}
.columnleft {
  margin-right : .5rem;
}
.columnTitle {
  margin-bottom : 1rem;
}
.columnleft .hoverDetail {left : 10%;}
.columnright .hoverDetail {right : 10%;}

.hoverDetail {
  padding : .5rem;
  position : absolute;
  width : 20%;
  min-width : 300px;
  bottom : 20%;
  display : none;
  background : rgba(0,0,0,0.85);
  max-height : 400px;
