}
.hoverDetailTop {
  display : flex;
  justify-content : space-between;
}
.hoverDetailTop .imgTextWrap {
  display : flex;
  width : 100%;
}
.hoverDetail .imgWrap {
  width : 10%;
  min-width : 30px;
}
.hoverDetailTop .imgWrap {
  width : 20%;
  min-width : 60px;
  margin-right : .5rem;
}
.hoverDetailTop .textWrap {
  width : calc(80% - .5rem - .1px);
  min-width : calc(100% - .5rem - 60.1px);
}
.hoverDetailTop .textWrap > div {
  font-size : .7rem;
}
.hoverDetailTop .textWrap > div:last-child {
  font-size : 1.05rem;
}

.hoverDetailBottom > div {
  margin-top : .7rem;
}
.hoverDetailBottom > div * {
  font-size : .9rem;
}


/* 템플릿 */
.itemPartBoxes > div, .indentStrings > div {
  margin-top : .5rem;
}

.hoverDetailQuality {
  position: relative;
  width: 100%;
  height: 15px;
  line-height: 12px;
  margin-top: .5rem;
  border: var(--contBox-border);
}
.hoverDetailQuality .qualityValue {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  z-index: -1;
}

.rune {margin-top : .5rem;}
.rune .textWrap * {font-size : .9rem;}
.rune .textWrap > div:last-child {font-size : .8rem;}
.columnList {
  position : relative;
  min-height : 52px;
  margin-bottom : .5rem;
  padding : .3rem;
  background : var(--bgLow);
  border-radius : var(--contBox-border-radius);
}
.columnList:last-child {
  margin-bottom : 0;
}
/* gifIng */
.lds-spinner {
  position: absolute;
  width: 80px;
  height: 80px;
  top : 50%;
  left : 50%;
  transform : translate(-50%, -50%);
}
.lds-spinner div {
  transform-origin: 40px 40px;
  -webkit-animation: lds-spinner 1.2s linear infinite;
          animation: lds-spinner 1.2s linear infinite;
}
.lds-spinner div:after {
  content: " ";
  display: block;
  position: absolute;
  top: 3px;
  left: 37px;
  width: 6px;
  height: 18px;
  border-radius: 20%;
  background: #fff;
}
.lds-spinner div:nth-child(1) {
  transform: rotate(0deg);
  -webkit-animation-delay: -1.1s;
          animation-delay: -1.1s;
}
.lds-spinner div:nth-child(2) {
  transform: rotate(30deg);
  -webkit-animation-delay: -1s;
          animation-delay: -1s;
}
.lds-spinner div:nth-child(3) {
  transform: rotate(60deg);
  -webkit-animation-delay: -0.9s;
          animation-delay: -0.9s;
}
.lds-spinner div:nth-child(4) {
  transform: rotate(90deg);
  -webkit-animation-delay: -0.8s;
          animation-delay: -0.8s;
}
.lds-spinner div:nth-child(5) {
  transform: rotate(120deg);
  -webkit-animation-delay: -0.7s;
          animation-delay: -0.7s;
}
.lds-spinner div:nth-child(6) {
  transform: rotate(150deg);
  -webkit-animation-delay: -0.6s;
          animation-delay: -0.6s;
}
.lds-spinner div:nth-child(7) {
  transform: rotate(180deg);
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}
.lds-spinner div:nth-child(8) {
  transform: rotate(210deg);
  -webkit-animation-delay: -0.4s;
          animation-delay: -0.4s;
}
.lds-spinner div:nth-child(9) {
  transform: rotate(240deg);
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}
.lds-spinner div:nth-child(10) {
  transform: rotate(270deg);
  -webkit-animation-delay: -0.2s;
          animation-delay: -0.2s;
}
.lds-spinner div:nth-child(11) {
  transform: rotate(300deg);
  -webkit-animation-delay: -0.1s;
          animation-delay: -0.1s;
}
.lds-spinner div:nth-child(12) {
  transform: rotate(330deg);
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}
@-webkit-keyframes lds-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes lds-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
